.blog{
	background-color: var(--secondaryColor);
	color:var(--secondaryFontColor);
	padding-top:5rem;
	padding-bottom: 5rem;
	h2{
		text-transform: uppercase;
	}
	p{
		margin-bottom: 1rem;
		margin-top: 1rem;
	}
	.masonryBlog{
		margin-top: 2rem;
		.my-masonry-grid {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			margin-left: -30px;
			width: auto;
		}
		.my-masonry-grid_column {
			padding-left: 30px;
			background-clip: padding-box;
		}
		.my-masonry-grid_column > div {
			margin-bottom: 30px;
		}
	}
	.blogWrapper{
		padding:1.5rem;
		transition: 0.2s;
		background: var(--primaryColor);
		border-radius: 0.2rem;
		position: relative;
		color:var(--primaryFontColor);
		overflow: hidden;
		&:hover{
			.blogSocialWrapper{
				display: flex;
				background-color: rgba(0,0,0,0.9);
				animation:moveRight 0.4s ease;
				color:#fff;
			}
		}
		
		.blogSocialWrapper{
			position: absolute;
			display: none;
			top:0;
			left:0;
			width: 100%;
			height: 100%;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			text-align: center;
			&.smallBlog{
				.blogProfile{
					height: 4rem;
					width: 4rem;
				}
				ul{
					margin-top: 1rem;
					li{
						height: 2rem;
						width: 2rem;
					}
				}
				.desc{
					margin-top: 0.5rem;
					h5{
						font-size: 16px;
					}
					p{
						margin-top: 0rem;
					}
				}
			}
			.desc{
				margin-top: 1rem;
				h5{
					font-size: 22px;
				}
				p{
					margin-top: 0rem;
				}
			}
			ul{
				display: flex;
				margin-top: 2rem;
				li{
					background-color: #fff;
					height: 2.5rem;
					width: 2.5rem;
					margin-right: 0.5rem;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 50%;
					cursor: pointer;
					&:last-child{
						margin-right: 0rem;
					}
				}
			}
			.blogProfile{
				height: 10rem;
				width: 10rem;
				border-radius: 50%;
				overflow: hidden;
				img{
					width: 100%;
					height: 100%;
					display: block;
				}
			}
		}
		.blogImg{
			width: 100%;
			margin-bottom: 2rem;
			img{
				width: 100%;
				max-width: 100%;
				max-height: 100%;
				display: block;
			}
		}
		p{
			line-height: 1.4;
			margin-bottom: 0rem;
		}
		.author{
			display: flex;
			margin-top: 1.4rem;
			flex-direction: column;
			align-items: flex-end;
			justify-content: flex-end;
			h5{
				font-size: 1rem;
			}
			p{
				margin-bottom: 0rem;
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
	}
}

@keyframes moveRight{
	0%{
		transform: translateX(-300px);
	}	
	100%{
		transform: translateX(0);
	}
}