.hero{
	padding-top: 5rem;
	position: relative;
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	padding-bottom: 5rem;
	.heroDots{
		position: absolute;
		right:2rem;
		top:2rem;
		@media(max-width:640px){
			display: none;
		}
		&>div{
			display: flex;
			margin-bottom: 1rem;
			align-items: center;
		}
		.dot{
			background-color: var(--secondaryColor);
			height: 0.5rem;
			padding:0.6rem;
			margin-right: 1rem;
			width: 0.5rem;
			display: block;
			border-radius: 50%;
		}
	}
	.agencyText{
		position: absolute;
	font-size: 140px;
	font-weight: 600;
	color:var(--secondaryColor);
	bottom:-2.5rem;
	@media(max-width:768px){
		font-size: 80px;
		bottom: -6rem;
	}
	@media(max-width:640px){
		font-size: 50px;
		display: none;
	}
} 
	.heroImg{
		width: 100%;
		height:35rem;
		display: flex;
		position: relative;
		justify-content: flex-end;
		overflow: hidden;
		@media(max-width:640px){
			margin-top: 2rem;
			height: 27rem;
		}
		img{
			max-width: 100%;
			max-height: 100%;
			display: block;
			border-radius: 0.3rem;
		}
	}
	.heroDesc{
		padding-right:3rem;
		height: 100%;
		margin-top: 7rem;
		@media(max-width:1024px){
			padding-right: 0rem;
		}
		@media(max-width:768px){
			margin-top: 0rem;
		}
		p{
			margin-top: 2rem;
			line-height: 1.4;
		}
		button{
			margin-top: 2rem;
		}
	}
	.heroDescImg{
		background-color: var(--secondaryColor);
		padding:1.5em;
		border-radius: 0.3rem;
		font-size: 1rem;
		line-height: 1.6;
		color:var(--secondaryFontColor);
		position: absolute;
		bottom:3rem;
		right:14rem;
		z-index: 1;
		@media(max-width:640px){
			left: 0rem;
			bottom:3rem;
		}
	}
}