.textBox{
	margin-bottom: 1rem;
	input{
		outline: none;
		min-height: 42px;
		border-radius: 0.3rem;
		width: 100%;
		border: 1px solid var(--secondaryFontColor);
		padding: 1rem;
		font-family: 'Montserrat', sans-serif;
		background-color: var(--secondaryColor);
		color:var(--secondaryFontColor);
		
		&:focus{
			outline: none;
		}
	}
	.error{
		color:red;
		font-size: 12px;
		margin-top: 0.4rem;

	}
	.helpText{
		color:#ccc;
		font-size: 12px;
		margin-top: 0.4rem;

	}

}