@import "./bootstrapGrid";
@import "./slickStyle";
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Inter:wght@200;300;500;600&family=Montserrat:wght@400;600&display=swap');
*{
	box-sizing: border-box;
	padding: 0;
	margin:0;
}
body{
	font-family: 'Montserrat', sans-serif;
	padding:0;
	margin:0;
	letter-spacing: 1px;
	background-color: var(--mainBgColor);
}
html {
	scroll-behavior: smooth;
  }
.container{
	max-width: 1200px;
	margin:0 auto;
	@media(max-width:1024px){
		max-width: 980px;
	}
	@media(max-width:768px){
		max-width: 700px;
	}
	@media(max-width:640px){
		max-width: 580px;
	}
	@media(max-width:425px){
		max-width: 90%;
	}
}
.mainHeading{
	font-size: 2.5rem;
}
.primaryBtn{
	background-color: var(--themeMainColor);
	color:var(--themeFontColor);
	font-size: 14px;
	cursor: pointer;
	padding:0.5rem 1rem;
	font-family: 'Montserrat', sans-serif;
	border: none;
	outline: none;
	letter-spacing: 1px;
	border-radius: 3rem;
}
.linkBtn{
	color:var(--primaryFontColor);
	background-color: transparent;
	border: none;
	font-family: 'Montserrat', sans-serif;
	font-size: 14px;
	font-weight: 600;
	cursor: pointer;
}
button{
	cursor: pointer;
	&:focus{
		outline: none;
	}
}
p{
	font-size: 14px;
}
a{
	text-decoration: none;
}
li{
	list-style-type: none;
}
.iconButton{
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 3rem;
	min-width: 3rem;
	background-color: #000;
	color:#fff;
	border-radius: 50%;
	border:none;
	cursor: pointer;
}