.headerWrapper{
	background-color: var(--secondaryColor);
	color:var(--secondaryFontColor);
	display: flex;
	min-height: 4rem;
	align-items: center;
	justify-content: space-between;
	padding:0rem 2rem;
	transition: 0.3s;

	@media(max-width:768px){
		padding:0rem 1.5rem;
	}
	@media(max-width:640px){
		padding:0rem 1rem;
	}
	.logo{
		font-weight: 600;
		font-size: 1.5rem;
	}
	&.stickyHeader{
		position: fixed;
		width: 100%;
		z-index: 100;
		animation:moveBottom 0.3s ease;
	}
	.menu{
		.deskMenu{
			display: flex;
			@media(max-width:1024px){
				display: none;
			}
			li{
				list-style-type: none;
				cursor: pointer;
				transition: 0.3s all;
				padding:0.5rem 1rem;
				&:hover{
					background-color: var(--themeMainColor);
					color:var(--themeFontColor);
					border-radius: 0.3rem;
				}
			}
		}
		.mobMenu{
			display: none;
			@media(max-width:1024px){
				display: block
			}
			.iconLinkBtn{
				background-color: transparent;
				border: none;
			}
		}
		.mobMenuList{
			position: absolute;
			width: 100%;
			left:0;
			top:4rem;
			background-color: var(--primaryColor);
			color:var(--primaryFontColor);
			z-index:1;
			li{
				list-style-type: none;
				padding:0.5rem;
				&:hover{
					background-color: var(--themeMainColor);
					color:var(--themeFontColor);
				}
			}
			
		}
	}
}

@keyframes moveBottom{
	0%{
		transform: translateY(-80px);
	}
	100%{
		transform: translateY(0px);

	}
}