.service{
	background-color: var(--secondaryColor);
	padding-top: 5rem;
	perspective: 1000px;
	padding-bottom: 5rem;
	color:var(--secondaryFontColor);
	h2{
		margin-bottom: 1rem;
		text-transform: uppercase;
	}
	.serviceDesc{
		margin-bottom: 3rem;
	}
	.serviceWrapper{
		border:1px solid var(--secondaryFontColor);
		padding:2rem;
		transition: 0.3s ease;
		margin-bottom: 1rem;
		&:hover{
			transform: scale(1.1);

			&:after {
				content: '';
				position: absolute;
				width: 100%;
				height: 8px;
				border-radius: 50%;
				left: 0;
				bottom: 0px;
				box-shadow: 0 30px 20px rgba(0, 0, 0, .3);
			}
		}
		h5{
			font-size: 20px;
			font-weight: 600;
		}
		p{
			margin-top: 2rem;
			margin-bottom: 0rem;
			line-height: 1.5;
		}
	}

}