.project{
	padding-top: 5rem;
	padding-bottom: 5rem;
	background-color: var(--primaryColor);
	color:Var(--primaryFontColor);
	h2{
		text-transform: uppercase;
	}
	p{
		margin-top: 1rem;
		margin-bottom: 3rem;
	}
	.projectWrapper{
		width:100%;
		margin-bottom: 3rem;
		height:30rem;
		position: relative;
		@media(max-width:640px){
			height: 25rem;
		}
		&:hover{
			.projectTitle{
				background-color: var(--primaryColor);
				color:Var(--primaryFontColor);
				padding:1rem;
				border:1px solid var(--primaryColor);
				width:100%;
				opacity: 1;
				animation: moveTop 0.4s ease;
			}
		}
		.projectImg{
			height: 100%;
			transition: 0.2s ease;
			width: 100%;
			img{
				max-width: 100%;
				height: 100%;
				width: 100%;
			}
		}
		.projectTitle{
			position: absolute;
			opacity: 0;
			bottom: 0;
			h3{
				margin-bottom: 1rem;
			}
			p{
				line-height: 1.4;
				margin-bottom: 1.5rem;
			}
		}
	}
}
@keyframes moveTop{
	0%{
		bottom:-3%
	}
	100%{
		bottom:0;
	}
}