.pricing{
	padding-top: 5rem;
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	padding-bottom: 5rem;
	.pricingTitle{
		margin-bottom: 3rem;
		text-transform: uppercase;
	}
	.intro-plan{
		margin-bottom: 1rem;
		padding-bottom: 1rem;
		border-bottom: 1px solid #000;
		display: inline-flex;
	}
	.planWrapper{
		border-radius: 0.3rem;
		border:1px solid #f0f0f0;cursor: pointer;
		padding:1.5rem;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 1.4rem;
		&.selected{
			background-color: var(--themeMainColor);
			color:var(--themeFontColor);
		}
		&:hover{
			border:1px solid #000;
		}
		.planName{
			display: flex;
			align-items: center;
			.planHeading{
				font-weight: 600;

				p{
					font-weight: 400;
					margin-top: 0.3rem;
				}
			}
		}
	}
	.listOfDetails{
		ul{
			li{
				list-style-type: none;
				padding-top:1rem;
				padding-bottom: 1rem;
				display: flex;
				align-items: center;
				&>div{
					margin-right: 0.5rem;
					svg{
						color:green
					}
				}
			}
		}
	}
}