.feedBack{
	padding-top: 5rem;
	padding-bottom: 9rem;
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	h2{
		margin-bottom: 1rem;
		text-transform: uppercase;
	}
	p{
		margin-bottom: 3rem;
	}
	.feedbackWrapper{
		
		background-color: var(--secondaryColor);
		color:var(--secondaryFontColor);
		border-radius: 0.4rem;
		transition: 0.3s;
		padding: 3rem;
		position: relative;
		margin-right: 1rem;
		overflow: hidden;
		.feedbackProfile{
			display: none;
			position: absolute;
			top:0;
			left:0;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			height: 100%;
			width: 100%;
			animation:moveBottom 0.4s ease;
			.profileImg{
				height: 4rem;
				width: 4rem;
				border-radius: 50%;
				overflow: hidden;
				img{
					height: 100%;
					width: 100%;
					display: block;
				}
			}
			.profileDetails{
				text-align: center;
				h4{
					margin-top: 1rem;
				}
				label{
					font-size: 13px;
					letter-spacing: 1px;
				}
			}
		}
		.quote{
			fill:#ccc;
			height:2.5em;
			width:2.5rem;
			opacity: 0.6;
			position: absolute;
			&.top{
				transform: rotate(180deg);

				top:8px;
				left:8px;			
			}
			&.bottom{
				bottom:10px;
				right:10px;
			}
		}
		&:hover{
				color:#fff;
				.feedbackProfile{
				background-color: rgba(0,0,0,0.9);

					display: flex;
				}
			}
		.feedbackDetails{
				line-height: 1.6;
				margin-bottom: 0rem;
			
		}
	}
}

@keyframes moveBottom{
	0%{
		transform: translateY(-400px);
	}
	100%{
		transform: translateY(0px);

	}
}