.aboutUs{
	background-color: var(--primaryColor);
	@media(max-width:640px){
		padding-bottom: 0rem;
	}
	position: relative;
	.descAbout{
		margin-top: 1rem;
	}
	.aboutImg{
		width: 100%;
		height: 50rem;
		@media(max-width:1024px){
			height: 37rem;
		}
		@media(max-width:768px){
			height: 28rem;
		}
		@media(max-width:768px){
			height: auto;
		}
		img{
			width: 100%;
			display: block;
		}
	}
	.aboutDesc{
		position: absolute;
		left:-50%;
		bottom:6rem;
		background-color: var(--secondaryColor);
		color:var(--secondaryFontColor);
		padding:3rem;
		border-radius: 0.5rem;
		@media(max-width:1024px){
			padding:2rem;
			top:10rem;
			bottom: auto;
		}
		@media(max-width:768px){
			top:5rem;
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}
		@media(max-width:640px){
			width: 100%;
			left:0;
			top:0;
			position: relative;
		}
		h2{
			text-transform: uppercase;
		}
		p{
			margin-bottom: 0rem;
			line-height: 1.5;
		}
	}
	.circles{
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		@media(max-width:768px){
			display: none;
		}
	}
	
	.circles li{
		position: absolute;
		display: block;
		list-style: none;
		width: 20px;
		height: 20px;
		background: rgba(240, 240, 240, 1);
		animation: animate 25s linear infinite;
		bottom: -150px;
		
	}
	
	.circles li:nth-child(1){
		right: 25%;
		width: 80px;
		height: 80px;
		animation-delay: 0s;
	}
	
	
	.circles li:nth-child(2){
		right: 10%;
		width: 20px;
		height: 20px;
		animation-delay: 2s;
		animation-duration: 12s;
	}
	
	.circles li:nth-child(3){
		right: 20%;
		width: 20px;
		height: 20px;
		animation-delay: 4s;
	}
	
	.circles li:nth-child(4){
		right: 20%;
		width: 60px;
		height: 60px;
		animation-delay: 4s;
		animation-duration: 18s;
	}
	
	.circles li:nth-child(5){
		right: 18%;
		width: 20px;
		height: 20px;
		animation-delay: 2s;
	}
	
	.circles li:nth-child(6){
		right: 12%;
		width: 110px;
		height: 110px;
		animation-delay: 3s;
	}
	
	.circles li:nth-child(7){
		right: 25%;
		width: 100px;
		height: 100px;
		animation-delay: 10s;
		animation-duration: 25s;
	}
	
	.circles li:nth-child(8){
		right: 9%;
		width: 25px;
		height: 25px;
		animation-delay: 15s;
		animation-duration: 45s;
	}
	
	.circles li:nth-child(9){
		left: 20%;
		width: 15px;
		height: 15px;
		animation-delay: 2s;
		animation-duration: 35s;
	}
	
	.circles li:nth-child(10){
		left: 10%;
		width: 10px;
		height: 10px;
		animation-delay: 0s;
		animation-duration: 15s;
	}
	
}


	
	
@keyframes animate {
	
	0%{
		transform: translateY(0) rotate(0deg);
		opacity: 1;
		border-radius: 0;
	}

	100%{
		transform: translateY(-1000px) rotate(720deg);
		opacity: 0;
		border-radius: 50%;
	}

}