.contact{
	background-color: var(--secondaryColor);
	padding-top: 5rem;
	padding-bottom: 5rem;
	color:var(--secondaryFontColor);
	.contactTitle{
		margin-bottom: 2rem;
		text-transform: uppercase;
	}
	.contactWrapper{
		.contactDetail{
			color:var(--secondaryFontColor);
			h3{
			margin-bottom: 2rem;
		}
		.submitButton{
			display: flex;
			align-items: center;margin-top: 1rem;
			justify-content: flex-end;
			button{
				background-color: var(--themeMainColor);
			}
		}
	}
		.contactLocation{
			color:var(--secondaryFontColor);
		}
	}
	.textArea{
		font-family: 'Montserrat', sans-serif;
		min-height: 140px;
		border: 1px solid var(--secondaryFontColor);
		width: 100%;
		padding:1rem;
		background-color: var(--secondaryColor);
		color:var(--secondaryFontColor);
		border-radius: 0.3rem;
		&:focus{
			outline: none;
		}
	}
	.contactLocation{
		ul{
			li{
				list-style-type: none;
				display: flex;
				padding:1rem;
				.icon{
					width: 3rem;
					display: flex;
					align-items: flex-start;
					svg{
						color:var(--themeMainColor);
					}
				}
				a{
					text-decoration: none;
					color:var(--secondaryFontColor);
				}
			}
		}
	}
}